* {
  font-family: 'Quicksand', sans-serif;
}

.invisible {
    display: none;
}

.button {
  margin: 10px;
  padding: 12px 12px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: all 60ms ease-in-out;
  -o-transition: all 60ms ease-in-out;
  transition: all 60ms ease-in-out;
  text-align: center;
  white-space: nowrap;
  text-decoration: none !important;
  text-transform: none;
  text-transform: capitalize;
  color: #fff;
  border: 0 none;
  border-radius: 4px;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.3;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex: 0 0 40px;
      flex: 0 0 40px;
  -webkit-box-shadow: 2px 5px 10px rgba(22, 22, 22, .1);
          box-shadow: 2px 5px 10px rgba(22, 22, 22, .1);
  width: 100px;
}
.button:hover {
  -webkit-transition: all 60ms ease;
  -o-transition: all 60ms ease;
  transition: all 60ms ease;
  opacity: 0.85;
}
.button:active {
  -webkit-transition: all 60ms ease;
  -o-transition: all 60ms ease;
  transition: all 60ms ease;
  opacity: 0.75;
}
.button:focus {
  outline: 1px dotted #959595;
  outline-offset: -4px;
}
.button.-regular {
  color: #202129;
  background-color: #edeeee;
}
.button.-regular:hover {
  color: #202129;
  background-color: #e1e2e2;
  opacity: 1;
}
.button.-regular:active {
  background-color: #d5d6d6;
  opacity: 1;
}
.button.-green {
  color: #fff;
  background: #e1e2e2;
}

.button.-green:hover{
    color: #202129;
    background:#3dd28d;
}
.button.-blue {
  color: #fff;
  background: #e1e2e2;
}
.button.-blue:hover{
    color: #202129;
    background:#416dea;
 }



